const setupNavbarAnim = () => {
  const navbar = document.querySelector('#navbar[data-anim]');

  window.addEventListener('scroll', () => {
    const scrollTop = window.scrollY;
    if (scrollTop > 40) {
      navbar?.classList.remove('clear');
    } else {
      navbar?.classList.add('clear');
    }
  });
};

export default setupNavbarAnim;
