const setupDropdown = () => {
  const dropdowns = document.querySelectorAll('.dropdown');
  const navbar = document.querySelector('#navbar');

  const handleClick = (e) => {
    const dropdown = e.currentTarget.parentElement;

    if (dropdown.classList.contains('open')) {
      dropdown.classList.remove('open');
      navbar.classList.add('h-navbar');
      navbar.classList.remove('open');
    } else {
      dropdown.classList.add('open');
      navbar.classList.remove('h-navbar');
      navbar.classList.add('open');
      // navbar.classList.remove('clear');
    }
  };

  dropdowns.forEach((item) => item.querySelector('button').addEventListener('mouseenter', handleClick));
};

export default setupDropdown;
