const setupLexyPhotos = () => {
  const btns = document.querySelectorAll('[data-for-lexy]');
  const imgs = document.querySelectorAll('[data-lexy]');

  const clearImgs = () => {
    imgs.forEach((img) => { img.style.opacity = '0'; });
  };

  const handleHover = (e) => {
    const btn = e.currentTarget;
    const string = btn.getAttribute('data-for-lexy');
    clearImgs();
    const cimg = Array.from(imgs).find((img) => img.getAttribute('data-lexy') === string);
    cimg.style.opacity = '1';
  };

  btns.forEach((btn) => btn.addEventListener('mouseover', handleHover));
};

export default setupLexyPhotos;
