const setupSelector = () => {
  const selectors = document.querySelectorAll('.selector');

  selectors?.forEach((selector) => {
    const res = selector.querySelector('.selector-res span');
    const options = selector.querySelector('.options').querySelectorAll('span');
    const input = selector.querySelector('input');

    const clearOptions = () => {
      options.forEach((option) => {
        option.classList.remove('active');
      });
    };
    const handleOptionClick = (option) => {
      const value = option.getAttribute('data-value');
      clearOptions();
      option.classList.add('active');
      res.textContent = value;
      input.value = value;
    };

    if (options) handleOptionClick(options[0]);

    options.forEach((option) => option.addEventListener('click', (e) => handleOptionClick(e.currentTarget)));
  });
};

export default setupSelector;
